import { HttpClient } from '../http-client';
import type {
  AddObjectsToListRequest,
  Object,
  PaginatedResponse,
  View,
} from '../types';

export class ListsResource {
  constructor(private httpClient: HttpClient) {}

  async getViews(
    spaceId: string,
    listId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<View>> {
    return this.httpClient.get<PaginatedResponse<View>>(
      `/v1/spaces/${spaceId}/lists/${listId}/views`,
      params,
    );
  }

  async getObjects(
    spaceId: string,
    listId: string,
    viewId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<Object>> {
    return this.httpClient.get<PaginatedResponse<Object>>(
      `/v1/spaces/${spaceId}/lists/${listId}/views/${viewId}/objects`,
      params,
    );
  }

  async addObjects(
    spaceId: string,
    listId: string,
    request: AddObjectsToListRequest,
  ): Promise<string> {
    return this.httpClient.post<string>(
      `/v1/spaces/${spaceId}/lists/${listId}/objects`,
      request,
    );
  }

  async removeObject(
    spaceId: string,
    listId: string,
    objectId: string,
  ): Promise<string> {
    return this.httpClient.delete<string>(
      `/v1/spaces/${spaceId}/lists/${listId}/objects/${objectId}`,
    );
  }
}
