import { HttpClient } from '../http-client';
import type {
  Member,
  MemberResponse,
  PaginatedResponse,
} from '../types';

export class MembersResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    spaceId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<Member>> {
    return this.httpClient.get<PaginatedResponse<Member>>(
      `/v1/spaces/${spaceId}/members`,
      params,
    );
  }

  async get(spaceId: string, memberId: string): Promise<MemberResponse> {
    return this.httpClient.get<MemberResponse>(
      `/v1/spaces/${spaceId}/members/${memberId}`,
    );
  }
}
