import { HttpClient } from './http-client';
import { AuthResource } from './resources/auth';
import { SpacesResource } from './resources/spaces';
import { ObjectsResource } from './resources/objects';
import { SearchResource } from './resources/search';
import { PropertiesResource } from './resources/properties';
import { TagsResource } from './resources/tags';
import { TypesResource } from './resources/types';
import { TemplatesResource } from './resources/templates';
import { MembersResource } from './resources/members';
import { ListsResource } from './resources/lists';

export interface AnytypeClientConfig {
  baseURL: string;
  apiKey?: string;
  timeout?: number;
  headers?: Record<string, string>;
}

export class AnytypeClient {
  private httpClient: HttpClient;
  public readonly auth: AuthResource;
  public readonly spaces: SpacesResource;
  public readonly objects: ObjectsResource;
  public readonly search: SearchResource;
  public readonly properties: PropertiesResource;
  public readonly tags: TagsResource;
  public readonly types: TypesResource;
  public readonly templates: TemplatesResource;
  public readonly members: MembersResource;
  public readonly lists: ListsResource;

  constructor(config: AnytypeClientConfig) {
    this.httpClient = new HttpClient(config);
    this.auth = new AuthResource(this.httpClient);
    this.spaces = new SpacesResource(this.httpClient);
    this.objects = new ObjectsResource(this.httpClient);
    this.search = new SearchResource(this.httpClient);
    this.properties = new PropertiesResource(this.httpClient);
    this.tags = new TagsResource(this.httpClient);
    this.types = new TypesResource(this.httpClient);
    this.templates = new TemplatesResource(this.httpClient);
    this.members = new MembersResource(this.httpClient);
    this.lists = new ListsResource(this.httpClient);
  }

  setApiKey(apiKey: string): void {
    this.httpClient.setApiKey(apiKey);
  }
}
