import { HttpClient } from '../http-client';
import type {
  CreateTagRequest,
  PaginatedResponse,
  Tag,
  TagResponse,
  UpdateTagRequest,
} from '../types';

export class TagsResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    spaceId: string,
    propertyId: string,
  ): Promise<PaginatedResponse<Tag>> {
    return this.httpClient.get<PaginatedResponse<Tag>>(
      `/v1/spaces/${spaceId}/properties/${propertyId}/tags`,
    );
  }

  async get(
    spaceId: string,
    propertyId: string,
    tagId: string,
  ): Promise<TagResponse> {
    return this.httpClient.get<TagResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}/tags/${tagId}`,
    );
  }

  async create(
    spaceId: string,
    propertyId: string,
    request: CreateTagRequest,
  ): Promise<TagResponse> {
    return this.httpClient.post<TagResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}/tags`,
      request,
    );
  }

  async update(
    spaceId: string,
    propertyId: string,
    tagId: string,
    request: UpdateTagRequest,
  ): Promise<TagResponse> {
    return this.httpClient.patch<TagResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}/tags/${tagId}`,
      request,
    );
  }

  async delete(
    spaceId: string,
    propertyId: string,
    tagId: string,
  ): Promise<TagResponse> {
    return this.httpClient.delete<TagResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}/tags/${tagId}`,
    );
  }
}
