import { HttpClient } from '../http-client';
import type {
  CreateObjectRequest,
  ObjectResponse,
  PaginatedResponse,
  UpdateObjectRequest,
  Object,
} from '../types';

export class ObjectsResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    spaceId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<Object>> {
    return this.httpClient.get<PaginatedResponse<Object>>(
      `/v1/spaces/${spaceId}/objects`,
      params,
    );
  }

  async get(spaceId: string, objectId: string): Promise<ObjectResponse> {
    return this.httpClient.get<ObjectResponse>(
      `/v1/spaces/${spaceId}/objects/${objectId}`,
    );
  }

  async create(
    spaceId: string,
    request: CreateObjectRequest,
  ): Promise<ObjectResponse> {
    return this.httpClient.post<ObjectResponse>(
      `/v1/spaces/${spaceId}/objects`,
      request,
    );
  }

  async update(
    spaceId: string,
    objectId: string,
    request: UpdateObjectRequest,
  ): Promise<ObjectResponse> {
    return this.httpClient.patch<ObjectResponse>(
      `/v1/spaces/${spaceId}/objects/${objectId}`,
      request,
    );
  }

  async delete(spaceId: string, objectId: string): Promise<ObjectResponse> {
    return this.httpClient.delete<ObjectResponse>(
      `/v1/spaces/${spaceId}/objects/${objectId}`,
    );
  }
}
