import { HttpClient } from '../http-client';
import type {
  CreateTypeRequest,
  PaginatedResponse,
  Type,
  TypeResponse,
  UpdateTypeRequest,
} from '../types';

export class TypesResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    spaceId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<Type>> {
    return this.httpClient.get<PaginatedResponse<Type>>(
      `/v1/spaces/${spaceId}/types`,
      params,
    );
  }

  async get(spaceId: string, typeId: string): Promise<TypeResponse> {
    return this.httpClient.get<TypeResponse>(
      `/v1/spaces/${spaceId}/types/${typeId}`,
    );
  }

  async create(
    spaceId: string,
    request: CreateTypeRequest,
  ): Promise<TypeResponse> {
    return this.httpClient.post<TypeResponse>(
      `/v1/spaces/${spaceId}/types`,
      request,
    );
  }

  async update(
    spaceId: string,
    typeId: string,
    request: UpdateTypeRequest,
  ): Promise<TypeResponse> {
    return this.httpClient.patch<TypeResponse>(
      `/v1/spaces/${spaceId}/types/${typeId}`,
      request,
    );
  }

  async delete(spaceId: string, typeId: string): Promise<TypeResponse> {
    return this.httpClient.delete<TypeResponse>(
      `/v1/spaces/${spaceId}/types/${typeId}`,
    );
  }
}
