import { HttpClient } from '../http-client';
import type {
  CreateSpaceRequest,
  PaginatedResponse,
  Space,
  SpaceResponse,
  UpdateSpaceRequest,
} from '../types';

export class SpacesResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<Space>> {
    return this.httpClient.get<PaginatedResponse<Space>>(
      '/v1/spaces',
      params,
    );
  }

  async get(spaceId: string): Promise<SpaceResponse> {
    return this.httpClient.get<SpaceResponse>(`/v1/spaces/${spaceId}`);
  }

  async create(request: CreateSpaceRequest): Promise<SpaceResponse> {
    return this.httpClient.post<SpaceResponse>('/v1/spaces', request);
  }

  async update(
    spaceId: string,
    request: UpdateSpaceRequest,
  ): Promise<SpaceResponse> {
    return this.httpClient.patch<SpaceResponse>(
      `/v1/spaces/${spaceId}`,
      request,
    );
  }
}
