import { HttpClient } from '../http-client';
import type {
  ObjectWithBody,
  PaginatedResponse,
  TemplateResponse,
} from '../types';

export class TemplatesResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    spaceId: string,
    typeId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<ObjectWithBody>> {
    return this.httpClient.get<PaginatedResponse<ObjectWithBody>>(
      `/v1/spaces/${spaceId}/types/${typeId}/templates`,
      params,
    );
  }

  async get(
    spaceId: string,
    typeId: string,
    templateId: string,
  ): Promise<TemplateResponse> {
    return this.httpClient.get<TemplateResponse>(
      `/v1/spaces/${spaceId}/types/${typeId}/templates/${templateId}`,
    );
  }
}
