import { HttpClient } from '../http-client';
import type {
  CreatePropertyRequest,
  PaginatedResponse,
  Property,
  PropertyResponse,
  UpdatePropertyRequest,
} from '../types';

export class PropertiesResource {
  constructor(private httpClient: HttpClient) {}

  async list(
    spaceId: string,
    params?: { limit?: number; offset?: number },
  ): Promise<PaginatedResponse<Property>> {
    return this.httpClient.get<PaginatedResponse<Property>>(
      `/v1/spaces/${spaceId}/properties`,
      params,
    );
  }

  async get(spaceId: string, propertyId: string): Promise<PropertyResponse> {
    return this.httpClient.get<PropertyResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}`,
    );
  }

  async create(
    spaceId: string,
    request: CreatePropertyRequest,
  ): Promise<PropertyResponse> {
    return this.httpClient.post<PropertyResponse>(
      `/v1/spaces/${spaceId}/properties`,
      request,
    );
  }

  async update(
    spaceId: string,
    propertyId: string,
    request: UpdatePropertyRequest,
  ): Promise<PropertyResponse> {
    return this.httpClient.patch<PropertyResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}`,
      request,
    );
  }

  async delete(spaceId: string, propertyId: string): Promise<PropertyResponse> {
    return this.httpClient.delete<PropertyResponse>(
      `/v1/spaces/${spaceId}/properties/${propertyId}`,
    );
  }
}
